* {
    font-family: 'odin';
    font-size: 1.03rem;
}

@font-face {
    font-family: 'odin';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

body {
    margin: 0;
    color: white;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

body::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-size: cover;
    filter: grayscale(100%) brightness(70%);
    background-position: center;
    z-index: -1;
}

.container {
    position: relative;
    z-index: 1;
}

.main-container {
    background-color: rgb(145, 145, 147);
    width: 80vw;
    height: 90vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    overflow-y: auto;
    border: 0.7rem solid black;
    border-radius: 0.5rem;
    box-shadow: 0 0 0.5rem 0.1rem rgb(34, 33, 33);
}

.task-form-div {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    display: grid;
    justify-self: center;
    align-self: stretch;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    padding: 0.5;
}

.task-form {
    display: grid;
    height: 13rem;
    margin-top: 0.3rem;
    margin-left: 0.3rem;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.3);
}

.odin-lined {
    height: 14vw;
    justify-self: center;
    align-self: center;
}

.logo {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
    justify-items: center;
    justify-self: center;
}

.text {
    display: grid;
    align-self: center;
    justify-items: start;
}

.task-form-text {
    margin: 0px;
    font-family: 'odin';
    font-size: 4.6vw;
    font-weight: bold;
    white-space: nowrap;
}

.task-form-text2 {
    margin: 0px;
    font-family: 'odin';
    font-size: 3vw;
}



.display-projects {
    background-color: rgb(44, 44, 45);
    border-top: 0.4rem solid rgb(0, 0, 0);
    margin-top: 0.5rem;
    grid-row: 2 / 4;
    grid-column: 1 / 3;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(4, 1fr);
}

.new-project-form {
    align-self: center;
    margin-bottom: 0.2rem;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-rows: 1fr 1fr;
}

.new-project-input {
    width: 90%;
    align-self: end;
    margin-bottom: 0.3rem;
}

.new-project {
    align-self: start;
}

.project-list {
    grid-column: 1 / 2;
    grid-row: 2 / 5;
    display: grid;
    justify-items: start;
    align-items: center;
    align-content: start;
    overflow-y: auto;
}

.project-element {
    display: grid;
    grid-template-columns: auto repeat(2, 1fr);
    gap: 0.1rem;
    padding: 0.2vw;
    margin-bottom: 0.4rem;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    background-color: rgb(145, 145, 147);
    border-radius: 0.2rem;
    white-space: nowrap;
}

.deleteProjectBtn, .sortBtn {
    height: 1rem;
    font-size: 0.6rem;
    padding: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.default-project {
    font-family: 'odin';
    border-left: 0.3rem solid black;
    grid-column: 2 / 6;
    grid-row: 1 / 5;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 1fr;
    grid-auto-flow: row;
    overflow-y: auto;
    justify-items: center;
    align-items: center;
    font-size: 2rem;
    text-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5);
      
}
/* .newTodo Grid */
.newTodo {
    font-size: 1rem;
    color: white;
    justify-self: center; /* Align horizontally (start, center, end, stretch) */
    align-self: center;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
    padding-left: 0.2rem;
    /* padding-right: 3.2rem; */
    width: 13rem;
    max-width: 15rem;
    height: 6.5rem;
    margin: 7px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
    border-radius: 0.1rem;
    border: 1px solid rgb(145, 145, 147);
    overflow-y: auto;
    display: grid;
    grid-auto-rows: min-content;
    grid-template-columns: repeat(4, auto);
    align-content: start;
    justify-content: start;
}

.addToProjectBtn {
    height: 1rem;
    font-size: 0.8rem;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
}

.deleteTodoBtn {
    height: 1rem;
    font-size: 0.8rem;
    margin-left: 0.2rem;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    margin-right: 0.5rem;
}

.selectProjectList {
    height: 1rem;
    font-size: 0.8rem;
    grid-column: 3 / 4;
    grid-row: 1 / 2;
    margin-right: 0.5rem;
    width: 3.7rem;
}

.completedCheckbox {
    vertical-align: middle;
    grid-column: 4 / 5;
    grid-row: 1 / 2;
}

.title {
    width: 11.5rem;
    margin-left: 0.2rem;
    grid-column: 1 / 5;
    grid-row: 2 / 3;
}
.title::before {
    content: attr(data-text);
    color: red;
}

.dueDate {
    margin-left: 0.2rem;
    grid-column: 1 / 5;
    grid-row: 3 / 4;
}

.description {
    margin-left: 0.2rem;
    grid-column: 1 / 5;
    grid-row: 5 / 6;
    width: 11.5rem;
    height: auto;

}

.priority {
    margin-left: 0.2rem;
    grid-column: 1 / 5;
    grid-row: 6 / 7;
}

.expandBtn {
    background: transparent;
    border: none;
    box-shadow: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    margin-left: 0.2rem;
    grid-column: 1 / 2;
    grid-row: 4 / 5;
    justify-self: start;
}

input {
    border-radius: 0.2rem;
    margin-bottom: 0.2rem;
    border: 1px solid black;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
}

h2 {
    margin-bottom: 0.2rem;
}

button {
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);

}

.project-element:hover {
    cursor: pointer;
}

#footer {
    margin-top: 0.2rem;
    margin-bottom: -0.8rem;
}
.pulse-heart {
    display: inline-block;
    animation: pulse 1.5s infinite;
}

@keyframes pulse {
    0% {
        transform: scale(0.8);
    }
    50% {
        transform: scale(1);
    }
    100% {
        transform: scale(0.8);
    }
}

.devil {
    animation: dev 1s infinite;
    z-index: 1;
    position: absolute;
    top: 1rem;
    color: red;
    height: 40rem;

}

@keyframes dev {
    0% {
        transform: scale(0.1);
    }
    50% {
        transform: scale(0.5);
    }
    100% {
        transform: scale(1);
    }
}



